export const useTableHandler = (editor) => {
  const handleTableClick = () => {
    if (!editor) return;

    if (editor.isActive('table')) {
      alert('      .');
      return;
    }

    editor
      .chain()
      .focus()
      .insertTable({ rows: 3, cols: 3, withHeaderRow: true })
      .run();
  };

  return {
    handleTableClick
  };
};